import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import VueAxios from 'vue-axios'
import { createStore } from 'vuex'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'

// Axios defaults
axios.defaults.headers.common['Accept'] = 'application/json';
axios.defaults.baseURL = 'https://api.kipekee.co.ke';

// Set authorization header
axios.interceptors.request.use(function (config) {
  const user = localStorage.getItem('user')
  
  if (user !== null) {
      config.headers.Authorization = `Bearer ${JSON.parse(localStorage.getItem('user')).token}`;
  }

  return config;
}, function (error) {
  return Promise.reject(error);
});

// store instance setup
const store = createStore({
    state () {
      return {
        page_props: [],
        loading: true,
        featured: {},
        properties: {},
        modal_list: {},
        user: {}
      }
    },
    mutations: {
      setProps(state, page_props) {
        state.page_props = page_props;
      },
      setLoading(state, loading) {
        state.loading = loading
      },
      setFeatured(state, featured) {
        state.featured = featured
      },
      setProperty(state, properties) {
        state.properties = properties
      },
      setModalList(state, modal_list) {
        state.modal_list = modal_list
      },
      setUser(state, user) {
        state.user = user
      }              
    },
    actions: {
      async getProps(context) {
        axios.get('api/page-prop?page=Home').then((response) => {
          const page_props = response.data.data
          page_props.services['service-items'] = page_props['service-items']
          page_props.partners['partner-items'] = page_props['partner-items']
          page_props.testimonials['testimonial-items'] = page_props['testimonial-items']

          // Footer
          page_props.footer['socials'] = page_props['socials']
          page_props.footer['navigation'] = page_props['navigation']
          context.commit("setProps", page_props);

          setTimeout(() => (context.commit("setLoading", false)), 2000)

        }).catch(() => {
          
        })
      },
      async getFeatured(context) {
        axios.get('api/featured').then((response) => {
          context.commit("setFeatured", response.data.data);
        }).catch(() => {
          
        })
      },
      async getProperties(context) {
        axios.get('api/property?size=25&page=0').then((response) => {
          context.commit("setProperty", response.data.data);
        }).catch(() => {
          
        })
      },
      async getUser(context) {
        axios.get('api/user').then((response) => {
          context.commit("setUser", response.data.data);
        }).catch(() => {
          context.commit("setUser", {})
          
        })
      },      
      getModalList(context, data) {
        context.commit("setModalList", data);
      }
    },
    getters: {
      page_props: state => state.page_props,
      featured: state => state.featured,
      properties: state => state.properties,
      modal_list: state => state.modal_list,
      user: state => state.user
    }
})
  
createApp(App)
    .use(router)
    .use(VueAxios, axios)
    .use(store)
    .component('font-awesome-icon', FontAwesomeIcon)
    .mount('#app')
